@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{font-family:'Lato', sans-serif}.App{align-items:flex-start;box-sizing:border-box;display:flex;flex-direction:row;min-height:100vh}#main{box-sizing:border-box;display:flex;flex-direction:column;width:calc(100% - 220px)}#main-content{background-color:#E8EFF5;min-height:calc(100vh - 72px);padding:30px}#main-content h1{margin:0}

.nav-item{border:0px solid #34C692;-webkit-transition:all 0.2s linear;transition:all 0.2s linear}.nav-item a{color:#7F7F7F;display:inline-block;height:100%;text-decoration:none;text-transform:capitalize;-webkit-transition:all 0.2s linear;transition:all 0.2s linear;width:100%;padding:15px 15px 15px 30px;height:100%}.nav-item:hover{border-color:#82debd;border-left-width:5px}.nav-item:hover a{padding-left:25px;color:#82debd}.nav-item.selected{border-color:#34C692 !important;border-left-width:5px}.nav-item.selected a{padding-left:25px;color:#34C692 !important}

.nav-search{background-color:#29333F;border:none;box-sizing:border-box;color:#7F7F7F;font-family:'Lato', sans-serif;height:40px;margin-bottom:20px;padding-left:60px;width:100%}.nav-search:focus{outline:none}.nav-search-container{position:relative}.search-icon{color:#7F7F7F;position:absolute;top:11px;left:30px}

#main-nav{box-sizing:border-box;background-color:#313D4B;width:220px;display:fixed;height:100vh}#main-nav-header{color:#FFFEFF;text-align:center;text-transform:uppercase;margin:25px 0}#main-nav-header h1{font-size:18px;margin:0}

.card{background:#FFFEFF;border-radius:3px;box-sizing:border-box;box-shadow:0 0 3px #7F7F7F;overflow:hidden}.card .card-header{background:#F4F4F4;padding:15px;color:#313D4B}.card .card-content{padding:15px}.card.halfwidth{width:50%}.card.fullwidth{width:100%}

.table{border-collapse:collapse;width:100%}.table thead{border-top:1px solid #E8EFF5;border-left:1px solid #E8EFF5;border-right:1px solid #E8EFF5;box-shadow:0 5px 5px #E8EFF5}.table thead th{color:#34C692;font-weight:400;line-height:1.4;background-color:transparent;padding-top:18px;padding-bottom:18px;text-transform:capitalize}.table tbody tr{border-bottom:1px solid #E8EFF5}.table tbody tr td{background-color:transparent;font-weight:300;line-height:1.4;padding-top:18px;padding-bottom:18px;text-align:center}.table tbody tr td .edit-icon{color:#34C692}.table tbody tr td .delete-icon{color:#D6425C}.table tbody tr:last-child{border:none}

#main-header{align-items:center;box-sizing:border-box;box-shadow:0 0 3px #313D4B;display:flex;height:72px;padding:30px}

